  export async function deleteFromChatroom(chatroomId,id ){
    const chatroomUpgrade = await api.deleteChatroomById(chatroomId).then(res=>{
      if(res.data.success){
        const update = getUser(id)
        return update
      }
    })
    return chatroomUpgrade
    
  }




  



